import { $$, Limiter } from 'substance'

export default function ContentBody (props) {
  const { centered } = props
  const el = $$('div', { className: 'sc-content-body' })
  if (centered) el.addClass('sm-centered')
  return (
    el.append(
      $$(Limiter, {},
        props.children
      )
    )
  )
}
